//# Maps
//### Shadow map
$shadow-map: (
    50: (
        distance: 15px,
        size: 40px
    ),
    40: (
        distance: 10px,
        size: 15px
    ),
    30: (
        distance: 5px,
        size: 10px
    ),
    20: (
        distance: 2px,
        size: 4px
    ),
    10: (
        distance: 1px,
        size: 1px
    )
);



//=== 3. Path variables
$image-path: '../img' !default;
$font-path: '../fonts' !default;
$icons-path: '#{$image-path}/vismaicons' !default;
$tabs-graph-path: $image-path + '/tabs' !default;



//=== 4. Typography 
$font-size-base: 14px;
$line-height-base: 145%;

$font-weight-base: normal;
$font-family-base: 'OpenSansFallback', 'Open Sans', sans-serif;
$font-family-secondary: 'UbuntuFallback', 'Ubuntu', sans-serif;



//### Main font-size
// font-weight
$bold: 700;
$semibold: 600;
$normal: 400;
$thin: 200;

// font-size/line-height font-family
$font-xl: 24px/30px $font-family-base;
$font-lg: 20px/30px $font-family-base;
$font-md: 16px/24px $font-family-base;
$font-sm: 14px/18px $font-family-base;
$font-xs: 12px/14px $font-family-base;



//=== 5. Responsivness
// main screen sizes
$screen-xxl-min: 1680px;
$screen-xl-min: 1440px;
$screen-lg-min: 1280px;
$screen-md-min: 992px;
$screen-sm-min: 769px;
$screen-xs-min: 550px;
 
$screen-xxl-max: 1679px;
$screen-xl-max: 1439px;
$screen-lg-max: 1279px;
$screen-md-max: 991px;
$screen-sm-max: 768px;
$screen-xs-max: 549px;



//=== 6. Grids
// Grid breakpoints
$grid-breakpoints: (
    xxs: 0,
    xs: $screen-xs-min,
    sm: $screen-sm-min,
    md: $screen-md-min,
    lg: $screen-lg-min,
    xl: $screen-xl-min,
    xxl: $screen-xxl-min
) !default;

// Grid containers
$container-max-widths: (
    xxs: 100%, 
    xs: 90%,
    sm: 90%,
    md: 90%,
    lg: $screen-lg-min - 30px,
    xl: $screen-xl-min - 30px,
    xxl: $screen-xxl-min - 30px 
    ) !default;

// Grid columns
// Set the number of columns and specify the width of the gutters.
$grid-columns:                12 !default;
$grid-gutter-width:           30px !default;

// Grid options - Quickly modify global styling by enabling or disabling optional features.
$enable-grid-classes:       true !default;



//=== 7. Z-index variable
$zindex-button-active:        5;
$zindex-button-focus:        10;
$zindex-navbar-header:      900;
$zindex-dropdown-backdrop:  990;
$zindex-dropdown:          1000;
$zindex-navbar:            1000;
$zindex-navbar-dropdown:   1010;
$zindex-fixed:             1030;
$zindex-sticky:            1030;
$zindex-modal-backdrop:    1040;
$zindex-modal:             1050;
$zindex-popover:           1060;
$zindex-tooltip:           1070;



//=== 8. Borders
$border-color: $neutral-50 !default;
$border-width: 1px !default;

$border-radius-5: 5px !default; 
$border-radius-10: 10px !default;  
$border-radius-20: 20px !default; 
$border-radius-circle: 100% !default; 
$border-radius: $border-radius-5 !default; 



//=== 9. Image thumbnails
$thumbnail-padding: .25rem !default;
$thumbnail-bg: $white !default;
$thumbnail-border-width: $border-width !default;
$thumbnail-border-color: $border-color !default;
$thumbnail-border-radius: $border-radius !default;
