
// Local Functions
// search icon for inputs background image string definition.
@function searchIcon($bg: $neutral-05) {
    $background-fill: str-replace('' + $bg, '#', '');
    $result: "data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2214%22%20height%3D%2214%22%20viewBox%3D%220%200%2014%2014%22%3E%3Cpath%20id%3D%22i%22%20d%3D%22M12.116%2C13.657%2C9.4%2C10.94a6%2C6%2C0%2C1%2C1%2C1.551-1.556l2.716%2C2.72a1.088%2C1.088%2C0%2C0%2C1%2C0%2C1.552%2C1.035%2C1.035%2C0%2C0%2C1-.744.34A1.3%2C1.3%2C0%2C0%2C1%2C12.116%2C13.657ZM2%2C5.99a4%2C4%2C0%2C1%2C0%2C4-4A4%2C4%2C0%2C0%2C0%2C2%2C5.99ZM5.741%2C3.158A8.531%2C8.531%2C0%2C0%2C0%2C3.75%2C7.818C2.5%2C6.369%2C3.71%2C3.308%2C5.741%2C3.158Z%22%20fill%3D%22%23" + $background-fill + "%22%2F%3E%3C%2Fsvg%3E";
    
    @return $result;
    }


//Search field style
.search-group {
    position: relative;

    // input basic styling
    input.form-control[type=text],
    input.form-control[type=search] {
        padding-right: 50px;
        padding-left: 36px;

        color: $neutral-70;

        border-radius: 24px;
        background-color: $neutral-05;
        box-shadow: shadow(20) rgba($blue-90, 0.12);

        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;

        &.hover, &:hover { box-shadow: shadow(20) rgba($primary-blue, 0.20); }

        &.focus, &:focus {
            color: $primary-neutral;
            box-shadow: inset 0 0 0 1px $blue-50, shadow(20) rgba($blue-50, 0.15);
        }

        // search input on disabled state
        .disabled &, :disabled &,
        &.disabled, &:disabled {
            pointer-events: none;
            color: $neutral-60;
            box-shadow: none;

            // search icon/button on disabled state
            ~ .search-icon{
                &::before { background-image: url(searchIcon($neutral-40)); }

                &.btn { pointer-events: none; }
            }

            // clear 'x' button, on disabled state
            ~ .clear-search.show {display: none;}
        }
    }

    // search icon/button and clear 'x' button basic style
    .search-icon,
    .clear-search {
        content: '';
        position: absolute;
        min-width: auto;
        min-width: unset;
        height: 22px;
        margin: 0;
        padding: 0;

        border: 0;
        box-shadow: none;
    }

    // search icon/button
    .search-icon {
        top: 2px;
        left: 6px;

        // search icon style definition
        &::before {
            display: block;
            position: absolute;
            opacity: 1;
            content: "";
            width: 14px;
            height: 14px;
            top: 6px;
            left: 6px;

            background: {
                color: transparent;
                image: url(searchIcon($neutral-70));
                position: 0 0;
                repeat: no-repeat;
            };
        }

        // search button style definition
        &.btn {
            cursor: pointer;
            width: 26px;
            height: 22px;
            top: 4px;
            right: 4px;
            left: initial;
            left: unset;

            border-radius: 30px;
            background-color: $neutral-05;
            box-shadow: none;

            &::before {
                width: 11px;
                height: 11px;
                top: 6px;
                left: 8px;

                background-size: 11px 11px;
            }
        }
    }

    // clear 'x' button
    .clear-search {
        display: none;
        cursor: pointer;
        top: 1px;
        right: 2px;

        // the 'x' icon style definition
        &::before {
            display: block;
            position: absolute;
            opacity: 1;
            content: "";
            width: 12px;
            height: 12px;
            top: 9px;
            right: 7px;

            background: {
                color: transparent;
                image: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%3E%3Cdefs%3E%3Cfilter%20id%3D%22ar%22%20x%3D%220%22%20y%3D%220%22%20width%3D%2212%22%20height%3D%2212%22%20filterUnits%3D%22userSpaceOnUse%22%3E%3CfeOffset%20dy%3D%221%22%20input%3D%22SourceAlpha%22%2F%3E%3CfeGaussianBlur%20stdDeviation%3D%220.5%22%20result%3D%22blur%22%2F%3E%3CfeFlood%20flood-color%3D%22%23163e59%22%20flood-opacity%3D%220.2%22%2F%3E%3CfeComposite%20operator%3D%22in%22%20in2%3D%22blur%22%2F%3E%3CfeComposite%20in%3D%22SourceGraphic%22%2F%3E%3C%2Ffilter%3E%3C%2Fdefs%3E%3Cpath%20id%3D%22a%22%20d%3D%22M8.124%2C8.849%2C4.5%2C5.224.875%2C8.849A.513.513%2C0%2C0%2C1%2C.15%2C8.123L3.775%2C4.5.15.875A.513.513%2C0%2C1%2C1%2C.876.15L4.5%2C3.774%2C8.124.149a.513.513%2C0%2C1%2C1%2C.725.725L5.225%2C4.5%2C8.849%2C8.123a.513.513%2C0%2C0%2C1-.725.725Z%22%20filter%3D%22url(%23ar)%22%20fill%3D%22%236a6c6d%22%2F%3E%3C%2Fsvg%3E");
                position: 0 0;
                repeat: no-repeat;
            };
        }

        &.show { display: block; }
    }

    // variant/primary style for search fields
    &.search-group-primary {
        input.form-control[type=text],
        input.form-control[type=search] {
            border-color: $green-60;
            box-shadow: shadow(20) rgba($green-60, 0.20);

            &.hover, &:hover {
                border-color: $green-40;
                box-shadow: shadow(20) rgba($green-60, 0.20);
            }

            &.focus, &:focus {
                border-color: $blue-50;
                box-shadow: inset 0 0 0 1px $blue-50, shadow(20) rgba($blue-50, 0.15);
            }

            // primary search fields on disabled state
            .disabled &, :disabled &,
            &.disabled, &:disabled {
                border-color: $neutral-40;
                box-shadow: none;

                ~ .search-icon.btn {
                    background-color: $green-40;
                    box-shadow: none;

                    &::before { background-image: url(searchIcon($green-20)); }
                }
            }
        }

        // search button for primary search fields
        .search-icon {
            &.btn {
                width: 42px;

                background-color: $green-70;
                box-shadow: shadow(20) rgba($green-80, 0.20);

                &::before {
                    left: 18px;

                    background: {
                        image: url(searchIcon());
                        size: 11px 11px;
                    };
                }
            }
        }
    }

    // search fields with button variant styling
    &.search-group-button {
        input.form-control[type=text],
        input.form-control[type=search] {
            padding-left: 18px;
        }

        // NOTE: Update/remove after design decision for search with button
        .clear-search.show {display: none;}
    }

    // larger search field style
    &.search-group-lg {
        input.form-control[type=text],
        input.form-control[type=search] {
            height: 48px;
            padding-right: 72px;
            padding-left: 50px;
        }

        // search icon/button for larger search field
        .search-icon {
            top: 12px;
            left: 18px;

            &.btn {
                height: 36px;
                width: 42px;
                top: 6px;
                right: 6px;
                left: initial;
                left: unset;

                &::before {
                    top: 12px;
                    left: 14px;
                }
            }

            &, &.btn {
                &::before {
                    width: 14px;
                    height: 14px;

                    background-size: 14px 14px;
                }
            }
        }

        .clear-search {
            top: 11px;
            right: 16px;
        }

        // larger search field for primary variant
        &.search-group-primary {
            .search-icon {
                &.btn {
                    width: 60px;

                    &::before { left: 24px; }
                }
            }
        }

        // larger search field with search button
        &.search-group-button {
            input.form-control[type=text],
            input.form-control[type=search] {
                padding-left: 28px;
            }
        }
    }
}

