
// Accordion / Collapsible list

.panel, 
.panel-group {
    .panel-heading {
        .panel-title { 
            [data-toggle="collapse"] {
                padding: 0 4px;
                margin: 0 -4px;

                .caret { margin-left: 8px; }
            }
        }

        // arrow styling for 'toggle' trigger
        [data-toggle="collapse"] {
            .caret {
                margin-right: 10px; 
            }

            // hover for default arrow/carot
            &:hover {
                .caret {
                    &:not([class*='caret-']) {
                        background-position-x: -84px;
                    }
                }
            }

            &:not(.collapsed) {
                .caret {
                    // background-position: -4px -41px;
                    &:not([class*='caret-']) {
                        background-position-y: -5px;
                    }
                }

                // large carrot
                .caret {
                    &[class*=caret-large] {
                        background-position-y: -6px;
                    }
                }

            }
        }
    }

    .panel-body {
        .panel-heading + .panel-collapse {
            .panel-body { padding-left: 20px; }
        }
    }
}

.panel-group {
    [data-toggle="collapse"] {
        display: inline-block;
        font-weight: $normal;
    }

    > .panel {
        &:last-child {
            > .panel-heading .collapsed {
                margin-bottom: 18px;
            }
        }

        > .panel-collapse {
            > .panel-body {
                padding-top: 0;
            }
        }
    }

    .panel-collapse {
        .panel-heading {
            .panel-title { 
                [data-toggle="collapse"] {   
                    &:not(.collapsed) { font-weight: $bold; }

                    .caret { margin-left: 2px; }
                }
            }
        }

        &.collapse {
            display: none;
    
            &.in {
                display: block;
            }
        }
    }
}
