    @return $result;
    }


//===
.breadcrumb {
    padding: 0 20px; 

    background: transparent;

    li {
        display: inline-block;
        font: $font-sm;

        > a {
            color: $blue-70;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
            }
        }

        &.active {
            &, & > a {
                color: $primary-neutral;
            }

            > a {
                pointer-events: none;
                cursor: default;

                &:hover {
                    text-decoration: none;
                }
            }
        }

        &.disabled {
            color: $neutral-60;

            > a {
                &, &:hover &:focus {
                    cursor: default;
                    outline: none;
                    color: $neutral-60;
                    text-decoration: none;
                }
            }

            + li::before {
                background-image: url(dividerIcon($neutral-60));
            }
        }

        + li::before {
            display: inline-block;
            position: relative;
            content: '';
            width: 5px;
            height: 8px;
            padding: 0;
            margin: 0 10px 0 6px;

            background: {
                color: transparent;
                image: url(dividerIcon($primary-neutral));
                position: 0 0;
                repeat: no-repeat;
            };
        }
    }
}