//=== Default style (white navigation)

.vertical-nav {
    background-color: $white;
    box-shadow: shadow(30) rgba($blue-90, 0.08);
	
    &:after {
        box-shadow: 0 0 20px rgba(#001F33, 0.1);
    }

    ul {
        li {
            a {
                color: $primary-neutral;

                &:hover { background-color: $blue-10; }

                &:after { background-position: -4px -32px; }
            }

            &.active {
                > a:only-child {
                    background-color: #dadfe5;
                    box-shadow: 5px 0 0 $green-60 inset;
                }
            }

            &.active,
            &.active.active-child {
                > a { 
                    background-color: $neutral-20; 
                    
                    &:hover { background-color: $blue-10; }
                }
            }

            &.disabled {
                > a { color: $neutral-60; }
            }

            &.show-nav {
                > a {
                    background-position: 0px 0px;

                    &:after { background-position: -8px -209px; }

                    &:hover { background-position: 0 -70px; }
                }
            }
        }
    } 

    ul {
        > li {
            > ul { background: $neutral-20; }
        }
    }
}

